/** @jsx jsx */
import { css, polished } from 'jimu-core';
export function sqlExpressionClauseRuntimeStyles(props) {
    const theme = props.theme;
    return css `
    margin: 10px 0;

    .sql-expression-label, .sql-expression-display-label{
      width: 100%;
      font-size: ${polished.rem(13)};
      color: ${theme.ref.palette.black};
    }
    .sql-expression-input{
      width: 100%;

    }
    .sql-expression-hint{
      margin-top: 4px;
      font-size: ${polished.rem(12)};
      color: ${theme.ref.palette.neutral[900]};
      font-style: italic;
    }

    .clause-inline{
      display: flex;
      justify-content: space-between;
      align-items: center;

      .sql-expression-label{
        width: calc(40% - 8px);
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
      }
      .sql-expression-label, .sql-expression-display-label{
        line-height: 32px;
      }
      .sql-expression-input{
        width: calc(60% - 8px);
      }
    }

  `;
}
