/** @jsx jsx */
import { css } from 'jimu-core';
export function predefinedSelector(props) {
    const theme = props.theme;
    return css `
    .predefined-list{
      min-height: 150px;

      .predefined-list-header{
        border-bottom: 1px solid ${theme.ref.palette.neutral[800]};
        line-height: 30px;

        .dragIcon{
          width: 26px;
        }
      }

      .predefined-list-content{
        max-height: calc(100vh - 255px);
        overflow-y: auto;

        .jimu-tree-item_template-card .jimu-tree-item__body{
          background-color: ${theme.ref.palette.neutral[500]};
        }

        .predefined-item{
          display: flex;
          flex-grow: 1;
          padding: 5px 0px;
          margin-bottom: 2px;
        }
      }

      .predefined-value, .predefined-label{
        width: 160px;
        &.value-input-isValid{
          outline: 1px solid ${theme.sys.color.error.light};

          button:focus, input:focus, .dropdown-button[aria-expanded="true"] {
            border: none !important;
          }
        }
        .predefined-button-con {
          margin-left: 1px;
        }
      }
      .predefined-label {
        margin-left: 1.5rem;
      }

      .predefined-cbx{
        display: flex;
        width: 150px;
        justify-content: center;
        align-items: center;
      }

      .predefined-delete{
        width:100px;
        text-align: right;
      }

    }

    .invalid-label{
      color: ${theme.sys.color.error.light};
      font-size: 12px;
      margin-top: '-32px';
      margin-bottom: '8px'
    }
  `;
}
