/** @jsx jsx */
import { css } from 'jimu-core';
export function pillSelectorStyles(props) {
    const theme = props.theme;
    return css `
    /** setting */
    .pill-selector-button{
      justify-content: space-between;
      display: flex;
      align-items: center;

      .pill-display-label{
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
        width: calc(100% - 20px);
      }
    }

    .pill-selector-popper{
      padding: 12px;
      max-width: 320px;
      max-height: 320px;
      overflow-y: auto;

      .jimu-btn{
        margin: 4px;
        max-width: 100%;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
      }
    }

    /** runtime */
    .pill-runtime-container{
      /* width: calc(100% + 10px); */
      width: 100%;
      .pill-btn-container{
        overflow-y: auto;
        margin-left: -5px;
        margin-top: -5px;

        .pill-btn{
          margin: 5px;
          max-width: calc(100% - 10px);
          white-space: nowrap;
          text-overflow: ellipsis;
          overflow: hidden;
          /* &:first-of-type{
            margin-left: 0;
          }
          &:last-of-type{
            margin-right: 0;
          } */

          &.pill-dash-btn{
            border: 1px dashed ${theme.sys.color.divider.tertiary};
          }
        }

        &.skeleton-btn-container {
          display: flex;
          flex-wrap: wrap;
          overflow-y: visible;
          div {
            margin: 5px;
            height: 32px;
            background-color: ${theme.ref.palette.neutral[300]};
            &.pill-skeleton1 {
              width: 72px;
            }
            &.pill-skeleton2 {
              width: 48px;
            }
            &.pill-skeleton3 {
              width: 64px;
            }
          }
        }
      }

      &.block-pills .pill-btn{
        display: block;
        margin-top: 10px;
      }
    }
  `;
}
